@customElement('sudoku-icon')
export class SudokuInputElement extends LitElement {
  static styles = [style];

  @property({ attribute: 'icon', type: String })
  accessor icon: undefined | keyof typeof icons;

  render() {
    if (!this.icon) {
      return null;
    }

    return html`<svg viewBox=${icons[this.icon].viewBox} xmlns="http://www.w3.org/2000/svg">
      ${icons[this.icon].svg}
    </svg>`;
  }
}
